table.statistics-confusion {
  position: relative;
  margin: auto;
  table-layout: fixed;
  border-collapse: collapse;
  color: #000;
}
table.statistics-confusion a {
  color: #000;
  text-decoration-style: dotted;
}
@media print {
  table.statistics-confusion a {
    text-decoration: none;
  }
}
table.statistics-confusion a:hover {
  color: #008;
  text-decoration: underline;
}
table.statistics-confusion a:hover:visited {
  color: #808;
}
table.statistics-confusion .term {
  display: inline-block;
}
table.statistics-confusion .term.highlight {
  background: rgba(238, 255, 136, 0.5);
  box-shadow: 0 0 0.2em 0.2em rgba(238, 255, 136, 0.5);
}
table.statistics-confusion .term[data-term='TP']::first-letter,
table.statistics-confusion .term[data-term='FN']::first-letter {
  color: #880000;
}
table.statistics-confusion .term[data-term='FP']::first-letter,
table.statistics-confusion .term[data-term='TN']::first-letter {
  color: #008800;
}
table.statistics-confusion .term[data-term='TP'],
table.statistics-confusion .term[data-term='FP'] {
  color: #cc4400;
}
table.statistics-confusion .term[data-term='FN'],
table.statistics-confusion .term[data-term='TN'] {
  color: #0044cc;
}
table.statistics-confusion .sfrac {
  display: inline-block;
  vertical-align: -0.6em;
  white-space: nowrap;
}
table.statistics-confusion .sfrac .num,
table.statistics-confusion .sfrac .den {
  display: block;
  text-align: center;
}
table.statistics-confusion .sfrac .den {
  border-top: solid 1px;
}
table.statistics-confusion .sfrac .aux {
  position: absolute;
  left: -10000px;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
table.statistics-confusion caption {
  height: 4rem;
  line-height: 4rem;
  font-size: 150%;
}
table.statistics-confusion td,
table.statistics-confusion th {
  width: 12rem;
  min-width: 12rem;
  height: 8rem;
  min-height: 8rem;
  text-align: center;
  border: solid 1px #888888;
  padding: 0;
}
table.statistics-confusion td.highlight,
table.statistics-confusion th.highlight {
  background: rgba(238, 255, 136, 0.4);
}
table.statistics-confusion td.actual-true .label,
table.statistics-confusion th.actual-true .label {
  color: #880000;
}
table.statistics-confusion td.actual-false .label,
table.statistics-confusion th.actual-false .label {
  color: #008800;
}
table.statistics-confusion td.predicted-true .label,
table.statistics-confusion th.predicted-true .label {
  color: #cc4400;
}
table.statistics-confusion td.predicted-false .label,
table.statistics-confusion th.predicted-false .label {
  color: #0044cc;
}
table.statistics-confusion td .formula,
table.statistics-confusion th .formula,
table.statistics-confusion td .input,
table.statistics-confusion th .input {
  font-weight: normal;
  margin-top: .5em;
}
table.statistics-confusion td .formula:first-child,
table.statistics-confusion th .formula:first-child,
table.statistics-confusion td .input:first-child,
table.statistics-confusion th .input:first-child {
  margin: 0;
}
table.statistics-confusion td .formula.smaller .term,
table.statistics-confusion th .formula.smaller .term {
  transform: scale(0.5);
}
table.statistics-confusion td .formula.larger .term,
table.statistics-confusion th .formula.larger .term {
  transform: scale(2);
}
table.statistics-confusion td .input .aux,
table.statistics-confusion th .input .aux {
  position: absolute;
  left: -10000px;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
table.statistics-confusion td .input input,
table.statistics-confusion th .input input {
  text-align: center;
  background: rgba(255, 255, 255, 0.5);
  border-width: 1px;
}
table.statistics-confusion td .input input:invalid,
table.statistics-confusion th .input input:invalid {
  background: #F8A;
}
@media print {
  table.statistics-confusion td .input input,
  table.statistics-confusion th .input input {
    display: none;
  }
}
@media not print {
  table.statistics-confusion td .input output,
  table.statistics-confusion th .input output {
    position: absolute;
    left: -10000px;
    width: 1px;
    height: 1px;
    overflow: hidden;
  }
}
table.statistics-confusion td .buttons,
table.statistics-confusion th .buttons {
  margin-top: .5em;
}
@media print {
  table.statistics-confusion td .buttons,
  table.statistics-confusion th .buttons {
    display: none;
  }
}
table.statistics-confusion td .buttons input,
table.statistics-confusion th .buttons input {
  transition: 0.5s;
  opacity: 0.1;
}
table.statistics-confusion td .buttons input:focus,
table.statistics-confusion th .buttons input:focus {
  opacity: 1;
}
table.statistics-confusion td:hover .buttons input,
table.statistics-confusion th:hover .buttons input {
  opacity: 1;
}
table.statistics-confusion tr:nth-child(2) td:nth-child(2) {
  border-top-width: 3px;
  border-left-width: 3px;
}
table.statistics-confusion tr:nth-child(2) td:nth-child(3) {
  border-top-width: 3px;
  border-right-width: 3px;
}
table.statistics-confusion tr:nth-child(3) td:nth-child(2) {
  border-bottom-width: 3px;
  border-left-width: 3px;
}
table.statistics-confusion tr:nth-child(3) td:nth-child(3) {
  border-bottom-width: 3px;
  border-right-width: 3px;
}
table.statistics-confusion button {
  position: absolute;
  width: 2rem;
  height: 2rem;
  font-size: 100%;
  line-height: 100%;
  padding: 0;
  border: solid 1px #888;
  color: #000;
  background: rgba(255, 255, 255, 0.75);
}
table.statistics-confusion button:hover,
table.statistics-confusion button:focus {
  background: #DDF;
}
table.statistics-confusion button.swap-rc {
  top: 3rem;
  left: -1rem;
}
table.statistics-confusion button.swap-c {
  top: 3rem;
  left: calc(23rem + 4px);
}
table.statistics-confusion button.swap-r {
  top: calc(19rem + 4px);
  left: -1rem;
}
table.statistics-confusion button.add-c {
  top: 3rem;
  left: calc(47rem + 8px);
}
table.statistics-confusion button.add-r {
  top: calc(35rem + 8px);
  left: -1rem;
}
@media print {
  table.statistics-confusion button {
    display: none;
  }
}
table.statistics-confusion button svg {
  display: block;
}
table.statistics-confusion .diagram {
  position: absolute;
  overflow: hidden;
  top: calc(12rem + 4px);
  left: calc(12rem + 4px);
  height: calc(16rem + 1px);
  width: calc(24rem + 1px);
}
table.statistics-confusion .diagram.base {
  z-index: -1;
}
table.statistics-confusion .diagram.labels {
  z-index: 1;
}
table.statistics-confusion .diagram .actual-true {
  fill: rgba(136, 0, 0, 0.2);
}
table.statistics-confusion .diagram .actual-false {
  fill: rgba(0, 136, 0, 0.2);
}
table.statistics-confusion .diagram .predicted-true {
  stroke: rgba(204, 68, 0, 0.5);
}
table.statistics-confusion .diagram .predicted-false {
  stroke: rgba(0, 68, 204, 0.5);
}
